@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{font-family:"Oswald", sans-serif;overflow-x:hidden}.App{text-align:center}.App .App-header{background-color:#fff;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.logo-image{height:20vh;width:20vw}.item{text-decoration:none;color:black}.navigation{display:flex;flex-direction:row;margin-bottom:3%}.navigation .navigation-sub{text-align:left;margin:auto;width:100%;display:flex;flex-direction:column;flex-wrap:wrap}

.thumbnail-link{text-decoration:none;color:black}.project{width:50%}.project-image{position:relative;height:60vh;overflow:hidden}.project-showcase{display:block;width:100%}.overlay{position:absolute;top:0;bottom:0;left:0;right:0;height:100%;width:100%;opacity:0;transition:0.5s ease;background-color:blue}.project-image:hover .overlay{opacity:1}.overlay-text{color:white;font-size:16px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center}.links{margin:auto;margin-top:3%;width:20%;display:flex;flex-direction:row;justify-content:space-evenly}.project-title{font-family:"Piazzolla", sans-serif;font-size:18px}

.thumbnails{width:100vw;display:flex;flex-direction:row;flex-wrap:wrap}

.skill{margin:auto;text-align:center;border:1px black solid;width:50vw;padding:1em;margin-bottom:2em;display:flex;flex-direction:column;justify-content:center;align-items:center}.skill .skill-graphic{margin-top:1em;width:20%}.pro{font-family:"Piazzolla", sans-serif;font-size:18px}

.profile{width:100%;margin:auto;display:flex;flex-direction:row;justify-content:space-evenly}.description{padding-left:3%;padding-right:3%;width:70%;border:1px black solid}.desc-heading,.hobbies{font-family:"Piazzolla", sans-serif;font-size:24px}


.homegraphic{width:40vw;height:60vh;margin:auto;display:flex;flex-direction:row;justify-content:space-evenly}


